import React, {useRef, useEffect, useState, Fragment} from 'react';

import Lottie from 'lottie-react';
import animationData from '../../data/Seccion_3/data.json';
import ConfirmarAsistenciaBoton from '../../components/ConfirmarAsistenciaBoton/ConfirmarAsistenciaBoton';

import "./Seccion_3.css"

const Seccion_3 = (props) => {

    const [animacion, setAnimacion] = useState(false);

    const refSeccion3 = useRef(null);

    const scrollCallback = (entries) => {
        if (entries[0].isIntersecting) {
            setAnimacion(true);
        }else{
            // do nothing
        }
    };

    useEffect(() => {
        // *** Grab the element related to this callback
        const { current } = refSeccion3;
        const observer = new IntersectionObserver(scrollCallback, {
            root: null,
            threshold: 1,
        });
        observer.observe(current);
        return () => {
            observer.disconnect(current); // *** Use the same element
        }
    }, [refSeccion3.current]); // *** Note dependency

    const styleBox = {
        top: 0,
        padding: 0,
        border: 0,
        "background-color": "transparent",
        width: "100px",
        height: "100px",
        position: "relative",
        margin: "auto",
        transform: "translate(0%, 400%)",
        zIndex: "-1000"
    };

    const styleImage = {
        top: 0,
        padding: 0,
        border: 0,
        height: "100vh",
        position: "absolute",
        margin: "auto",
        transform: "translate(-50%, 200%)",
        zIndex: "-100"
      };

    const styleImageHidden = {
        top: 0,
        padding: 0,
        border: 0,
        height: "100vh",
        position: "absolute",
        margin: "auto",
        transform: "translate(-50%, 200%)",
        visibility: "hidden",
        zIndex: "-200"
    };

    let styleImageShow = "";

    if(props.ancho > 600){
      styleImageShow = {
            top: 0,
            padding: 0,
            border: 0,
            height: "100vh",
            position: "absolute",
            margin: "auto",
            transform: "translate(-50%, 200%)",
            zIndex: "0"
          };
    }else {
      styleImageShow = {
            top: 0,
            padding: 0,
            border: 0,
            height: "100vh",
            position: "absolute",
            margin: "auto",
            transform: "translate(-50%, 200%)",
            zIndex: "0"
          };
    }

    const style = {
        top: 0,
        margin: '-1px 0px 0px 0px',
        padding: '0px 0px 0px 0px',
        border: '0px 0px 0px 0px',
        height: "100vh",
        width: '100%',
        transform: "translate(0%, 0%)",
      };  

    return (
        <Fragment>
            {/* <Lottie 
                loop
                autoplay
                animationData={animationData}
                style={style}
            /> */}

            <object id="textoIglesia" type="image/svg+xml" data="./img/Iglesia.svg"></object>
 
            <img id="fonddoSeccion3" src={props.gifSeccion3} style={animacion ? styleImageShow : styleImageHidden} alt="foto" />

            { animacion ? "" : <img id="fonddoSeccion3_1" src="./imgFondosSinAnimar/3.jpg" style={styleImage}></img> }

            <div id="seccion3Box" style={styleBox} ref={refSeccion3}></div>

        </Fragment>
    );
}
 
export default Seccion_3