import React, {useEffect, useState, Fragment} from 'react'
import { useLocation } from "react-router-dom";

import "./HorizontalScrollEcard.css"
// Json Lotties
import Seccion_1 from './Seccion_1/Seccion_1'
import Seccion_2 from './Seccion_2/Seccion_2'
import Seccion_3 from './Seccion_3/Seccion_3'
import Seccion_4 from './Seccion_4/Seccion_4'
import Seccion_5 from './Seccion_5/Seccion_5'
import Seccion_6 from './Seccion_6/Seccion_6'
import Seccion_7 from './Seccion_7/Seccion_7'
import Seccion_8 from './Seccion_8/Seccion_8'

import gifSeccion1 from './imgFondos/Iniciales.gif';
import gifSeccion2 from './imgFondos/foto.gif';
import gifSeccion3 from './imgFondos/Iglesia.gif';
import gifSeccion4 from './imgFondos/Fiesta.gif';
import gifSeccion5 from './imgFondos/Asistencia.gif';
import gifSeccion6 from './imgFondos/regalo.gif';
import gifSeccion7 from './imgFondos/Itinerario.gif';
import gifSeccion8 from './imgFondos/Cierre.gif';

import MensajeContextoProvider from "./Mensaje/MensajeContextoProvider";
import Funnel from '../funnel/Funnel';

const HorizontalScrollEcard = (props) => {

    let {search} = useLocation();
    let query = new URLSearchParams(search);    

    const [altura, setAltura] = useState(0)
    const [ancho, setAncho]   = useState(0)
  
    useEffect(() => {
        setAltura( window.innerHeight )
        const anchoAux = (window.innerHeight * 57) / 100
        setAncho( anchoAux )
        console.log( "altura: " + altura + "ancho: " + ancho  )
    }, [altura, ancho])

    const styleImage = {
        top: "40%",
        padding: 0,
        border: 0,
        height: "20vh",
        position: "absolute",
        margin: "auto",
        transform: "translate(-50%, 0%)",
        zIndex: "100"
      };
      
      const styleImageHidden = {
        top: "0%",
        padding: 0,
        border: 0,
        height: "90vh",
        position: "absolute",
        margin: "auto",
        transform: "translate(-50%, 0%)",
        visibility: "hidden",
        zIndex: "100"
      };  

        return ( 
            <Fragment>
                <MensajeContextoProvider>
                    <div id="fondoPantalla">
                        <div id="uno" className="tarjeta" >
                            <Seccion_1 altura={altura} ancho={ancho} gifSeccion1={gifSeccion1}/>
                        </div>
                        <div id="dos" className="tarjeta">
                            <Seccion_2 altura={altura} ancho={ancho} gifSeccion2={gifSeccion2}/>
                        </div>
                        <div id="tres" className="tarjeta">
                            <Seccion_3 altura={altura} ancho={ancho} gifSeccion3={gifSeccion3}/>
                        </div>
                        <div id="tres" className="tarjeta">
                            <Seccion_4 altura={altura} ancho={ancho} gifSeccion4={gifSeccion4}/>
                        </div>
                        <div id="tres" className="tarjeta">
                            <Seccion_5 altura={altura} ancho={ancho} gifSeccion5={gifSeccion5}/>
                        </div>
                        <div id="tres" className="tarjeta">
                            <Seccion_6 altura={altura} ancho={ancho} gifSeccion6={gifSeccion6}/>
                        </div>
                        <div id="tres" className="tarjeta">
                            <Seccion_7 altura={altura} ancho={ancho} gifSeccion7={gifSeccion7}/>
                        </div>  
                        <div id="tres" className="tarjeta">
                            <Seccion_8 altura={altura} ancho={ancho} gifSeccion8={gifSeccion8}/>
                        </div>  
                </div>
                {query.get("mkt") === null ? "" :  <Funnel/> }     
                </MensajeContextoProvider>
            </Fragment>                  
        );   
}
 
export default HorizontalScrollEcard;