
import React, {useRef, useEffect, useState, Fragment} from 'react';

import Lottie from 'lottie-react';
import animationData from '../../data/Seccion_7/data.json';

import "./Seccion_7.css"

const Seccion_7 = (props) => {

    const [animacion, setAnimacion] = useState(false);

    const refSeccion7 = useRef(null);

    const scrollCallback = (entries) => {
        if (entries[0].isIntersecting) {
            setAnimacion(true);
        }else{
            // do nothing
        }
    };

    useEffect(() => {
        // *** Grab the element related to this callback
        const { current } = refSeccion7;
        const observer = new IntersectionObserver(scrollCallback, {
            root: null,
            threshold: 1,
        });
        observer.observe(current);
        return () => {
            observer.disconnect(current); // *** Use the same element
        }
    }, [refSeccion7.current]); // *** Note dependency

    const styleBox = {
        top: 0,
        padding: 0,
        border: 0,
        "background-color": "transparent",
        width: "100px",
        height: "100px",
        position: "relative",
        margin: "auto",
        transform: "translate(0%, 400%)",
        zIndex: "-1000"
    };

    const styleImage = {
        top: 0,
        padding: 0,
        border: 0,
        height: "100vh",
        position: "absolute",
        margin: "auto",
        transform: "translate(-50%, 600%)",
        zIndex: "-100"
    };

    const styleImageHidden = {
        top: 0,
        padding: 0,
        border: 0,
        height: "100vh",
        position: "absolute",
        margin: "auto",
        transform: "translate(-50%, 600%)",
        visibility: "hidden",
        zIndex: "-200"
    };

    let styleImageShow = "";

    if(props.ancho > 600){
      styleImageShow = {
            top: 0,
            padding: 0,
            border: 0,
            height: "100vh",
            position: "absolute",
            margin: "auto",
            transform: "translate(-50%, 600%)",
            zIndex: "0"
          };
    }else {
      styleImageShow = {
            top: 0,
            padding: 0,
            border: 0,
            height: "100vh",
            position: "absolute",
            margin: "auto",
            transform: "translate(-50%, 600%)",
            zIndex: "0"
          };
    }

    const style = {
        top: 0,
        margin: '0px 0px 0px 0px',
        padding: '0px 0px 0px 0px',
        border: '0px 0px 0px 0px',
        height: "100vh",
        width: '100%',
        transform: "translate(0%, 0%)",
      };  

    return (
        <Fragment>
            {/* <Lottie 
                loop
                autoplay
                animationData={animationData}
                style={style}
            /> */}

            <img id="fonddoSeccion7" src={props.gifSeccion7} style={animacion ? styleImageShow : styleImageHidden} alt="foto" />

            { animacion ? "" : <img id="fonddoSeccion7_1" src="./imgFondosSinAnimar/7.jpg" style={styleImage}></img> }

            <object id="textoFotos" type="image/svg+xml" data="./img/Fotos.svg"></object>

            <div id="seccion7Box" style={styleBox} ref={refSeccion7}>{animacion}</div>
        </Fragment>

    );
}
 
export default Seccion_7