import React, {useRef, useEffect, useState, Fragment} from 'react';

import Lottie from 'lottie-react';
import animationData from '../../data/Seccion_8/data.json';

import Timer from '../Countdown/Timer';

import "./Seccion_8.css"

const Seccion_8 = (props) => {

    const [animacion, setAnimacion] = useState(false);

    const refSeccion8 = useRef(null);

    const scrollCallback = (entries) => {
        if (entries[0].isIntersecting) {
            setAnimacion(true);
        }else{
            // do nothing
        }
    };

    useEffect(() => {
        // *** Grab the element related to this callback
        const { current } = refSeccion8;
        const observer = new IntersectionObserver(scrollCallback, {
            root: null,
            threshold: 1,
        });
        observer.observe(current);
        return () => {
            observer.disconnect(current); // *** Use the same element
        }
    }, [refSeccion8.current]); // *** Note dependency

    const styleBox = {
        top: 0,
        padding: 0,
        border: 0,
        "background-color": "transparent",
        width: "100px",
        height: "100px",
        position: "relative",
        margin: "auto",
        transform: "translate(0%, 400%)",
        zIndex: "-1000"
    };

    const styleImage = {
        top: 0,
        padding: 0,
        border: 0,
        height: "100vh",
        position: "absolute",
        margin: "auto",
        transform: "translate(-50%, 700%)",
        zIndex: "-100"
    };

    const styleImageHidden = {
        top: 0,
        padding: 0,
        border: 0,
        height: "100vh",
        position: "absolute",
        margin: "auto",
        transform: "translate(-50%, 700%)",
        visibility: "hidden",
        zIndex: "-200"
    };

    let styleImageShow = "";

    if(props.ancho > 600){
      styleImageShow = {
            top: 0,
            padding: 0,
            border: 0,
            height: "100vh",
            position: "absolute",
            margin: "auto",
            transform: "translate(-50%, 700%)",
            zIndex: "0"
          };
    }else {
      styleImageShow = {
            top: 0,
            padding: 0,
            border: 0,
            height: "100vh",

            position: "absolute",
            margin: "auto",
            transform: "translate(-50%, 700%)",
            zIndex: "0"
          };
    }

    const style = {
        top: 0,
        margin: '-1px 0px 0px 0px',
        padding: '0px 0px 0px 0px',
        border: '0px 0px 0px 0px',
        height: "100vh",
        width: '100%',
        transform: "translate(0%, 0%)",
    };  

    const current = new Date();
    // it adds 15 days to a current date
    current.setDate(current.getDate()+17);
    //console.log(current);
    let ano = current.getUTCFullYear();
    let mes = current.getMonth() + 1;
    if(mes < 10) {
        mes = "0"+mes
    }

    let dia = current.getDate();

    if(dia < 10) {
        dia = "0"+dia
    }
    const fecha = ano+"-"+mes+"-"+dia+"T12:00:00.000Z";


    return (
        <Fragment>
            {/* <Lottie 
                loop
                autoplay
                animationData={animationData}
                style={style}
            /> */}

            {animacion ? 
                            <img id="fonddoSeccion2" src="./imgFondos/Cierre.gif" style={styleImage} alt=""></img> 
                        : 
                            <img id="fonddoSeccion2" src="./imgFondosSinAnimar/8.jpg" style={styleImage} alt=""></img> 
            }

            <img id="fonddoSeccion8" src={props.gifSeccion8} style={animacion ? styleImageShow : styleImageHidden} alt="foto" />

            { animacion ? "" : <img id="fonddoSeccion8_1" src="./imgFondosSinAnimar/8.jpg" style={styleImage} alt=""></img> }

            {/* <object id="textoCierre" type="image/svg+xml" data="./img/CierreTexto.svg">Texto</object> */}

            <div id="textoSeccion8">
                  <img src="./img/Contador.svg" alt=""/>
                  <div id="contenedorTimer" >
                    <Timer deadline={"2023-12-17T00:00:00.000Z"} />
                    {/* <Timer deadline={fecha} /> */}
                  </div>
            </div>  

            <div id="seccion8Box" style={styleBox} ref={refSeccion8}>{animacion}</div>

        </Fragment>

    );
}
 
export default Seccion_8