import React, {useState, useEffect, useRef, useContext, Fragment} from 'react';

import MensajeContexto from '../Mensaje/MensajeContexto';

import Lottie from 'lottie-react';
import animationData from '../../data/Seccion_2/data.json';

import "./Seccion_2.css"

const Seccion_2 = (props) => {

    const [animacion, setAnimacion] = useState(false);

    const refSeccion2 = useRef(null);

    const { mostrarMensaje, updateMostrarMensaje } = useContext(MensajeContexto);

    const scrollCallback = (entries) => {
        if (entries[0].isIntersecting) {
            updateMostrarMensaje(false);
            setAnimacion(true);
        }else{
            // do nothing
        }
    };

    useEffect(() => {
        // *** Grab the element related to this callback
        const { current } = refSeccion2;
        const observer = new IntersectionObserver(scrollCallback, {
            root: null,
            threshold: 1,
        });
        observer.observe(current);
        return () => {
            observer.disconnect(current); // *** Use the same element
        }
    }, [refSeccion2.current]); // *** Note dependency
    
    const styleBox = {
        top: 0,
        padding: 0,
        border: 0,
        "background-color": "transparent",
        width: "100px",
        height: "100px",
        position: "relative",
        margin: "auto",
        transform: "translate(0%, 400%)",
        zIndex: "-1000"
    };

    const styleImage = {
        top: 0,
        padding: 0,
        border: 0,
        height: "100vh",
        position: "absolute",
        margin: "auto",
        transform: "translate(-50%, 100%)",
        zIndex: "-100"
      };

      const styleImageHidden = {
        top: 0,
        padding: 0,
        border: 0,
        height: "100vh",
        position: "absolute",
        margin: "auto",
        transform: "translate(-50%, 100%)",
        visibility: "hidden",
        zIndex: "-200"
      };

      let styleImageShow = "";

      if(props.ancho > 600){
        styleImageShow = {
              top: 0,
              padding: 0,
              border: 0,
              height: "100vh",
              position: "absolute",
              margin: "auto",
              transform: "translate(-50%, 100%)",
              zIndex: "0"
            };
      }else {
        styleImageShow = {
              top: 0,
              padding: 0,
              border: 0,
              height: "100vh",
              position: "absolute",
              margin: "auto",
              transform: "translate(-50%, 100%)",
              zIndex: "0"
            };
      }

    const style = {
        top: 0,
        margin: '-1px 0px 0px 0px',
        padding: '0px 0px 0px 0px',
        border: '0px 0px 0px 0px',
        height: "100vh",
        width: '100%',
        transform: "translate(0%, 0%)",
        zIndex: "1000"
      };  

    return (
        <Fragment>
            {/* <Lottie 
                loop = "false"
                autoplay
                animationData={animationData}
                style={style}
            />  */}

            <img id="fonddoSeccion2" src={props.gifSeccion2} style={animacion ? styleImageShow : styleImageHidden} alt="foto"/>
            
            { animacion ? "" : <img id="fonddoSeccion2_1" src="./imgFondosSinAnimar/2.jpg" style={styleImage} alt=""></img> }

            <object id="textoFoto" type="image/svg+xml" data="./img/FotoTexto.svg">texto alternativo</object>

            <div id="seccion2Box" style={styleBox} ref={refSeccion2}></div>

        </Fragment>
    );
}
 
export default Seccion_2